// components/TaskForm.js
import { useEffect, useState } from "react";
import { API_BASE } from "./api";

/**
 * Props:
 *  - userId (number)
 *  - onCreated (fn) called after successful creation
 */
export default function TaskForm({ userId = 1, onCreated = () => {} }) {
  const [title, setTitle] = useState("");
  const [durationMinutes, setDurationMinutes] = useState(60);
  const [deadline, setDeadline] = useState("");
  const [priority, setPriority] = useState("");
  const [category, setCategory] = useState("");
  const [loading, setLoading] = useState(false);
  const [error, setError] = useState(null);

  // load defaultDuration from localStorage app_settings if available
  useEffect(() => {
    try {
      const raw = localStorage.getItem("app_settings");
      if (raw) {
        const parsed = JSON.parse(raw);
        if (parsed.defaultDuration) setDurationMinutes(parsed.defaultDuration);
      }
    } catch {}
  }, []);

  function resetForm() {
    setTitle("");
    setDurationMinutes(prev => prev || 60);
    setDeadline("");
    setPriority("");
    setCategory("");
  }

  async function handleSubmit(e) {
    e.preventDefault();
    setError(null);

    if (!title || title.trim() === "") {
      setError("Please enter a title.");
      return;
    }

    setLoading(true);
    try {
      // Send deadline as ISO string (backend converts to MySQL DATETIME)
      const payload = {
        user_id: userId,
        title: title.trim(),
        duration_minutes: Number(durationMinutes) || null,
        deadline: deadline ? new Date(deadline).toISOString() : null,
        priority: priority || null,
        category: category || null,
      };

      const res = await fetch(`${API_BASE}/api/tasks`, {
        method: "POST",
        headers: { "Content-Type": "application/json" },
        body: JSON.stringify(payload),
      });

      const json = await res.json();

      if (!res.ok) {
        const msg = (json && json.error) || "Failed to create task";
        throw new Error(msg);
      }

      // success
      resetForm();
      onCreated(json); // pass created task to parent
    } catch (err) {
      console.error("TaskForm create error:", err);
      setError(err.message || "Error creating task");
    } finally {
      setLoading(false);
    }
  }

  return (
    <div className="bg-white dark:bg-gray-800 p-4 rounded shadow dark:shadow-none text-gray-900 dark:text-gray-100">
      <h4 className="font-semibold mb-3">Create Task</h4>

      <form onSubmit={handleSubmit} className="space-y-3">
        <div>
          <label className="block text-sm text-gray-700 dark:text-gray-300">Title</label>
          <input
            value={title}
            onChange={(e) => setTitle(e.target.value)}
            placeholder="What do you need to do?"
            className="mt-1 w-full border p-2 rounded dark:bg-gray-700 dark:border-gray-600 dark:text-gray-100"
          />
        </div>

        <div className="grid grid-cols-2 gap-3">
          <div>
            <label className="block text-sm text-gray-700 dark:text-gray-300">Duration (min)</label>
            <input
              type="number"
              min={5}
              value={durationMinutes}
              onChange={(e) => setDurationMinutes(e.target.value)}
              className="mt-1 w-full border p-2 rounded dark:bg-gray-700 dark:border-gray-600 dark:text-gray-100"
            />
          </div>

          <div>
            <label className="block text-sm text-gray-700 dark:text-gray-300">Priority</label>
            <select
              value={priority}
              onChange={(e) => setPriority(e.target.value)}
              className="mt-1 w-full border p-2 rounded dark:bg-gray-700 dark:border-gray-600 dark:text-gray-100"
            >
              <option value="">—</option>
              <option value="high">High</option>
              <option value="medium">Medium</option>
              <option value="low">Low</option>
            </select>
          </div>
        </div>

        <div>
          <label className="block text-sm text-gray-700 dark:text-gray-300">Deadline (optional)</label>
          <input
            type="datetime-local"
            value={deadline}
            onChange={(e) => setDeadline(e.target.value)}
            className="mt-1 w-full border p-2 rounded dark:bg-gray-700 dark:border-gray-600 dark:text-gray-100"
          />
        </div>

        <div>
          <label className="block text-sm text-gray-700 dark:text-gray-300">Category</label>
          <input
            value={category}
            onChange={(e) => setCategory(e.target.value)}
            placeholder="e.g., Study, Exercise"
            className="mt-1 w-full border p-2 rounded dark:bg-gray-700 dark:border-gray-600 dark:text-gray-100"
          />
        </div>

        {error && <div className="text-sm text-red-500">{error}</div>}

        <div className="flex items-center gap-2">
          <button
            type="submit"
            disabled={loading}
            className="px-4 py-2 bg-indigo-600 text-white rounded disabled:opacity-60"
          >
            {loading ? "Saving..." : "Save Task"}
          </button>
          <button
            type="button"
            onClick={resetForm}
            className="px-3 py-2 border rounded text-sm"
          >
            Reset
          </button>
        </div>
      </form>
    </div>
  );
}
