import { useEffect, useState } from "react";

export default function SettingsModal({ open, onClose, onSave }) {
  const [defaultDuration, setDefaultDuration] = useState(60);
  const [enableNotifications, setEnableNotifications] = useState(true);
  const [theme, setTheme] = useState("system"); // light | dark | system

  // Load settings when modal opens
  useEffect(() => {
    if (!open) return;

    try {
      const raw = localStorage.getItem("app_settings");
      if (raw) {
        const s = JSON.parse(raw);
        setDefaultDuration(s.defaultDuration ?? 60);
        setEnableNotifications(s.enableNotifications ?? true);
        setTheme(s.theme ?? "system");
      }
    } catch {}
  }, [open]);

  function applyTheme(t) {
    const root = document.documentElement;
    root.classList.remove("dark");

    if (t === "dark") {
      root.classList.add("dark");
    } else if (t === "system") {
      const prefersDark = window.matchMedia("(prefers-color-scheme: dark)").matches;
      if (prefersDark) root.classList.add("dark");
    }
  }

  function save() {
    const settings = { defaultDuration, enableNotifications, theme };
    localStorage.setItem("app_settings", JSON.stringify(settings));
    applyTheme(theme);
    if (onSave) onSave(settings);
    onClose();
  }

  if (!open) return null;

  return (
    <div className="fixed inset-0 z-50 flex items-center justify-center">
      <div className="absolute inset-0 bg-black/50" onClick={onClose} />

      <div className="bg-white dark:bg-gray-800 text-gray-900 dark:text-gray-100 p-6 rounded-lg shadow-lg w-80 z-10">
        <h2 className="text-xl font-semibold mb-4">Settings</h2>

        <label className="block mb-4">
          <span className="text-sm">Default Task Duration (minutes)</span>
          <input
            type="number"
            min={5}
            value={defaultDuration}
            onChange={(e) => setDefaultDuration(Number(e.target.value))}
            className="mt-1 w-full p-2 border rounded dark:bg-gray-700 dark:border-gray-600"
          />
        </label>

        <label className="flex items-center gap-2 mb-4">
          <input
            type="checkbox"
            checked={enableNotifications}
            onChange={(e) => setEnableNotifications(e.target.checked)}
          />
          <span>Enable Notifications</span>
        </label>

        <div className="mb-4">
          <span className="text-sm">Theme</span>

          <div className="mt-2 space-y-2">
            <label className="flex items-center gap-2">
              <input
                type="radio"
                checked={theme === "light"}
                onChange={() => setTheme("light")}
              />
              Light
            </label>

            <label className="flex items-center gap-2">
              <input
                type="radio"
                checked={theme === "dark"}
                onChange={() => setTheme("dark")}
              />
              Dark
            </label>

            <label className="flex items-center gap-2">
              <input
                type="radio"
                checked={theme === "system"}
                onChange={() => setTheme("system")}
              />
              System
            </label>
          </div>
        </div>

        <div className="flex justify-end gap-2 mt-4">
          <button
            onClick={onClose}
            className="px-3 py-2 border rounded dark:border-gray-600"
          >
            Cancel
          </button>
          <button
            onClick={save}
            className="px-3 py-2 bg-indigo-600 text-white rounded"
          >
            Save
          </button>
        </div>
      </div>
    </div>
  );
}
