// components/MiniBarChart.js
import React from "react";

/**
 * Expects `data` to be an object mapping "YYYY-MM-DD" => minutes (number)
 * Example: { "2025-11-01": 120, "2025-11-02": 45, ... }
 *
 * If data is empty, shows placeholders.
 */
export default function MiniBarChart({ data = {} }) {
  // build last 7 days labels (sorted oldest -> newest)
  const DAYS = 7;
  const days = [];
  const today = new Date();
  for (let i = DAYS - 1; i >= 0; i--) {
    const d = new Date(today.getTime() - i * 24 * 3600 * 1000);
    const key = d.toISOString().slice(0, 10);
    days.push(key);
  }

  const values = days.map((k) => Math.round(data[k] || 0));
  const max = Math.max(...values, 1);

  return (
    <div className="bg-white dark:bg-gray-800 p-3 rounded shadow dark:shadow-none text-gray-900 dark:text-gray-100">
      <div className="flex items-center justify-between mb-2">
        <div className="text-sm font-semibold">Weekly Activity</div>
        <div className="text-xs text-gray-500 dark:text-gray-400">{values.reduce((a, b) => a + b, 0)} min</div>
      </div>

      <div className="h-20 flex items-end gap-2">
        {values.map((v, i) => {
          const h = (v / max) * 100;
          return (
            <div key={days[i]} className="flex flex-col items-center">
              <div
                title={`${days[i]}: ${v} min`}
                className={`w-3 rounded-t ${v === 0 ? "bg-gray-200 dark:bg-gray-700" : "bg-indigo-400 dark:bg-indigo-500"}`}
                style={{ height: `${Math.max(h, 3)}%` }}
              />
              <div className="text-xs text-gray-500 dark:text-gray-400 mt-1">{new Date(days[i]).toLocaleDateString(undefined, { weekday: "short" }).slice(0,3)}</div>
            </div>
          );
        })}
      </div>
    </div>
  );
}
